import React from 'react';

import {BrowserRouter as Router} from 'react-router-dom'

import axios from 'axios';
import qs from 'qs';
import pubsub from 'pubsub-js'
// 
axios.interceptors.request.use((config) => {

    //1token
    let user = window.localStorage.getItem('user');


    user = user ? qs.parse(user) : '';

    config.headers={'token': user.token};

    //loading...
    pubsub.publish('UPDATE_LOADING',true)
    return config;//2

}, function(error) {
    // 
    return Promise.reject(error);
});

//
axios.interceptors.response.use(function(response) {
    pubsub.publish('UPDATE_LOADING',false)

    let router=new Router();//Router BroswerRouter

    //token: 2,login
    if (response.data.err === 2 && !router.history.location.pathname.includes('/login')) {
        window.location.href=window.location.origin+'/login?path='+router.history.location.pathname

        /*router.history.push({  //hash history
          pathname: '/login',
          search: "path="+router.history.location.pathname
        })*/

    }
    return response;

}, function(error) {

    return Promise.reject(error);
});

React.axios = axios;//axios  React.axios
React.Component.prototype.axios = axios; // axiosComponent   |this.axios
window.axios = axios;  //   window.axios , webpack 
export default axios;